#footer {
  padding: 15px;
  font-size: 14px;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 300px;
  z-index: 9999;

  .copyright {
    text-align: center;
  }

  .credits {
    padding-top: 5px;
    text-align: center;
    font-size: 13px;
  }
}

@media screen and (max-width: 1199px) {
  #footer {
    position: static;
    width: auto;
    padding-right: 20px 15px;
  }
}
